@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap);
.addon{width:100%;height:100vh;position:fixed;z-index:-2;background-color:black;opacity:0;transition:all 350ms ease}.navigation{width:100%;height:65px;background-color:transparent;position:absolute;top:0}.navigation .logo{position:absolute;left:0;margin:10px;margin-left:30px;cursor:pointer}.navigation .logo a img{width:100px}.navigation .hamburger{float:right;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-right:30px;height:22px;margin-top:22px;cursor:pointer;-webkit-justify-content:space-between;justify-content:space-between}.navigation .hamburger .line{width:30px;height:3px;border-radius:3px;background-color:white}.navigation .navigationContent{float:right;background-color:white;height:100vh;width:300px;position:fixed;right:0;display:none;z-index:999;-webkit-animation:fromLeft 300ms linear forwards 1;animation:fromLeft 300ms linear forwards 1}.navigation .navigationContent .closeNav{position:relative;top:15px;left:20px;width:35px;height:12px;cursor:pointer}.navigation .navigationContent .closeNav .x{width:30px;height:2px;background-color:black;border-radius:3px;margin-top:10px}.navigation .navigationContent .closeNav .x1{-webkit-transform:rotate(-45deg);transform:rotate(-45deg);-webkit-animation:spin1 400ms linear forwards 1;animation:spin1 400ms linear forwards 1}.navigation .navigationContent .closeNav .x2{-webkit-transform:rotate(45deg);transform:rotate(45deg);margin-top:-2px}.navigation .navigationContent ul{display:-webkit-flex;display:flex;list-style:none;-webkit-flex-direction:column;flex-direction:column;font-size:1.1rem;position:absolute;text-align:center;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:100%}.navigation .navigationContent ul a{color:black;margin:20px;text-decoration:none;transition:all 300ms linear}.navigation .navigationContent ul a:hover{color:#79b2e2}@-webkit-keyframes fromLeft{from{-webkit-transform:translateX(300px);transform:translateX(300px)}to{-webkit-transform:translateX(0px);transform:translateX(0px)}}@keyframes fromLeft{from{-webkit-transform:translateX(300px);transform:translateX(300px)}to{-webkit-transform:translateX(0px);transform:translateX(0px)}}@-webkit-keyframes spin{from{-webkit-transform:rotate(360deg);transform:rotate(360deg)}to{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}}@keyframes spin{from{-webkit-transform:rotate(360deg);transform:rotate(360deg)}to{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}}*{margin:0;padding:0;box-sizing:border-box;font-family:Inter, Arial, Helvetica, sans-serif}.loadingSpinner{border:6px solid #ececec;border-radius:50%;border-top:6px solid #79b2e2;width:40px;height:40px;z-index:214;transition:all 300ms ease-in;-webkit-animation:spin 1s linear infinite;animation:spin 1s linear infinite}.website .hero .backgroundImage{background:linear-gradient(rgba(1,1,1,0.65), rgba(1,1,1,0.65)),url(/frozo/static/media/background.cef0a6f1.jpg);width:100%;height:100vh;position:relative;z-index:-1;background-position:center;background-size:cover;background-repeat:no-repeat}.website .hero h1{position:absolute;top:170px;font-size:3.3rem;left:30px;font-weight:600;width:800px;letter-spacing:5px;color:white}.website .hero .scroll{position:absolute;top:90%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.website .hero .scroll .arrow{width:55px;height:55px;animation:topDown 800ms linear infinite forwards alternate-reverse}.website .wrapper{max-width:1200px;margin:0 auto;text-align:center;margin-top:60px;margin-bottom:50px}@-webkit-keyframes topDown{from{-webkit-transform:translateY(-50px);transform:translateY(-50px)}to{-webkit-transform:translateY(0px);transform:translateY(0px)}}@keyframes topDown{from{-webkit-transform:translateY(-50px);transform:translateY(-50px)}to{-webkit-transform:translateY(0px);transform:translateY(0px)}}@media only screen and (max-width: 1000px){.website .hero h1{width:100%;padding-right:50px;font-size:2.2rem}}

